import { defineComponent, reactive, toRefs, useContext, watch } from '@nuxtjs/composition-api';
import relativeTime from 'dayjs/plugin/relativeTime';
import QuickSelect from '../../internal/date-time-range/quick-select.vue';
import TimeSelect from '../../internal/date-time-range/time-select.vue';
export default defineComponent({
    components: {
        QuickSelect,
        TimeSelect
    },
    props: {
        inputEditable: {
            type: Boolean,
            default: false
        },
        startDateValue: {
            type: String,
            default: ''
        },
        endDateValue: {
            type: String,
            default: ''
        },
        format: {
            type: String,
            default: 'YYYY-MM-DD @ HH:mm:ss'
        },
        defaultShortcut: {
            type: String,
            default: 'today'
        }
    },
    setup(props, { emit }) {
        const { $dayjs, i18n } = useContext();
        $dayjs.extend(relativeTime);
        const FORMAT = props.format;
        const state = reactive({
            startDate: new Date().toISOString(),
            endDate: new Date().toISOString(),
            startDatePicker: new Date().toISOString(),
            endDatePicker: new Date().toISOString(),
            startMenu: false,
            endMenu: false,
            showDate: true,
            shortcut: '',
            startDateMessage: '',
            endDateMessage: '',
            valid: true,
            mode: 'absolute',
            endDateInput: '',
            startDateInput: '',
            defaultRange: true
        });
        const onQuickSelect = (data) => {
            localStorage.setItem('range_picker_window_time', Math.abs(new Date(data.range[0]).getTime() - new Date(data.range[1]).getTime()).toString());
            // bind with datepicker
            state.startDatePicker = $dayjs(data.range[0]).format('YYYY-MM-DD');
            state.endDatePicker = $dayjs(data.range[1]).format('YYYY-MM-DD');
            // bind with datepicker
            state.startDate = data.range[0];
            state.endDate = data.range[1];
            state.showDate = false;
            state.shortcut = data.text;
            sync();
        };
        const onStartTimeSelect = (data) => {
            state.startDate = $dayjs(state.startDate)
                .set('hour', data.hour)
                .set('minute', data.minute)
                .set('second', 0)
                .$d.toISOString();
            sync();
        };
        const onEndTimeSelect = (data) => {
            state.endDate = $dayjs(state.endDate)
                .set('hour', data.hour)
                .set('minute', data.minute)
                .set('second', 0)
                .$d.toISOString();
            sync();
        };
        const onNextWindow = () => {
            const windowTime = localStorage.getItem('range_picker_window_time');
            state.startDate = new Date(new Date(state.startDate).getTime() + parseInt(windowTime)).toISOString();
            state.endDate = new Date(new Date(state.endDate).getTime() + parseInt(windowTime)).toISOString();
            state.showDate = true;
            sync();
        };
        const onPreviousWindow = () => {
            const windowTime = localStorage.getItem('range_picker_window_time');
            state.startDate = new Date(new Date(state.startDate).getTime() - parseInt(windowTime)).toISOString();
            state.endDate = new Date(new Date(state.endDate).getTime() - parseInt(windowTime)).toISOString();
            state.showDate = true;
            sync();
        };
        const onStartDatePickerInput = (data) => {
            const year = $dayjs(data).get('year');
            const month = $dayjs(data).get('month');
            const date = $dayjs(data).get('date');
            state.startDate = $dayjs(state.startDate)
                .set('year', year)
                .set('month', month)
                .set('date', date)
                .$d.toISOString();
            sync();
        };
        const onEndDatePickerInput = (data) => {
            const year = $dayjs(data).get('year');
            const month = $dayjs(data).get('month');
            const date = $dayjs(data).get('date');
            state.endDate = $dayjs(state.endDate)
                .set('year', year)
                .set('month', month)
                .set('date', date)
                .$d.toISOString();
            sync();
        };
        const onInputError = (type) => {
            const ERROR_MESSAGE = `${i18n.t('expected format')} ${FORMAT}`;
            if (type === 0) {
                state.startDateMessage = ERROR_MESSAGE;
            }
            else {
                state.endDateMessage = ERROR_MESSAGE;
            }
        };
        const onResetError = (type) => {
            if (type === 0) {
                state.startDateMessage = '';
            }
            else {
                state.endDateMessage = '';
            }
        };
        const saveStartDateInput = (date) => {
            const newDate = $dayjs(date).format(FORMAT);
            if (newDate === 'Invalid Date')
                return;
            state.startDatePicker = $dayjs(newDate).format('YYYY-MM-DD'); // bind with datepicker
            state.startDate = $dayjs(newDate).$d.toISOString();
            sync();
        };
        const saveEndDateInput = (date) => {
            const newDate = $dayjs(date).format(FORMAT);
            if (newDate === 'Invalid Date')
                return;
            state.endDatePicker = $dayjs(newDate).format('YYYY-MM-DD'); // bind with datepicker
            state.endDate = $dayjs(newDate).$d.toISOString();
            sync();
        };
        const onStartDateInput = (date) => {
            const newDate = $dayjs(date).format(FORMAT);
            if (newDate === 'Invalid Date')
                return onInputError(0);
            onResetError(0);
        };
        const onEndDateInput = (date) => {
            const newDate = $dayjs(date).format(FORMAT);
            if (newDate === 'Invalid Date')
                return onInputError(1);
            onResetError(1);
        };
        const setStartDateToNow = () => {
            const now = $dayjs();
            state.startDatePicker = now.format('YYYY-MM-DD'); // bind with datepicker
            state.startDate = $dayjs(now.format(FORMAT)).$d.toISOString();
            sync();
            state.startMenu = false;
        };
        const setEndDateToNow = () => {
            const now = $dayjs();
            state.endDatePicker = now.format('YYYY-MM-DD'); // bind with datepicker
            state.endDate = $dayjs(now.format(FORMAT)).$d.toISOString();
            sync();
            state.endMenu = false;
        };
        const onViewModeChange = (type) => {
            state.mode = type;
            state.showDate = true;
        };
        const getFormat = (date) => {
            if (state.mode === 'absolute')
                return $dayjs(date).format(FORMAT);
            return `~ ${$dayjs(date).fromNow()}`;
        };
        const inputSync = () => {
            state.endDateInput = $dayjs(state.endDate).format(FORMAT);
            state.startDateInput = $dayjs(state.startDate).format(FORMAT);
        };
        const sync = () => {
            emit('update:time-range', {
                startDate: state.startDate,
                endDate: state.endDate
            });
        };
        watch([() => state.startDate, () => state.endDate], () => {
            inputSync();
            if ($dayjs(state.startDate).isAfter(state.endDate)) {
                state.valid = false;
                return;
            }
            state.valid = true;
        }, { immediate: true });
        watch(() => props.startDateValue, () => {
            if (!props.startDateValue)
                return;
            state.defaultRange = false;
            const newValue = $dayjs(props.startDateValue).format(FORMAT);
            state.startDate = $dayjs(newValue).$d.toISOString();
        }, { immediate: true });
        watch(() => props.endDateValue, () => {
            if (!props.endDateValue)
                return;
            state.defaultRange = false;
            const newValue = $dayjs(props.endDateValue).format(FORMAT);
            state.endDate = $dayjs(newValue).$d.toISOString();
        }, { immediate: true });
        return {
            ...toRefs(state),
            onQuickSelect,
            onEndTimeSelect,
            onStartTimeSelect,
            sync,
            onNextWindow,
            onPreviousWindow,
            onStartDatePickerInput,
            onEndDatePickerInput,
            onStartDateInput,
            onEndDateInput,
            setStartDateToNow,
            setEndDateToNow,
            onViewModeChange,
            getFormat,
            saveStartDateInput,
            saveEndDateInput
        };
    }
});
