import { defineComponent, ref, watch, useContext } from '@nuxtjs/composition-api';
import useTimeSelect from '../../../compositions/useTimeSelect';
export default defineComponent({
    props: {
        date: {
            type: [String, Date],
            default: () => { }
        }
    },
    setup(props, { emit }) {
        const { times } = useTimeSelect();
        const { $dayjs } = useContext();
        const currentTime = ref(0);
        const onTimeClick = (time) => {
            emit('select', { hour: time.hour, minute: time.minute });
        };
        watch(() => props.date, () => {
            const hour = $dayjs(props.date).get('hour');
            const minute = $dayjs(props.date).get('minute');
            const roundMinute = minute < 30 ? 0 : 30;
            const current = times.find((time) => time.hour === hour && time.minute === roundMinute);
            currentTime.value = times.indexOf(current);
        }, { immediate: true });
        return { times, onTimeClick, currentTime };
    }
});
