import { defineComponent, onMounted, reactive, toRefs, useContext, watch } from '@nuxtjs/composition-api';
import relativeTime from 'dayjs/plugin/relativeTime';
import { useDateRange, Common } from '../../../compositions/useDateRange';
export default defineComponent({
    props: {
        defaultRange: {
            type: Boolean,
            default: true
        },
        defaultShortcut: {
            type: String,
            default: ''
        }
    },
    setup(props, { emit }) {
        const state = reactive({
            menu: false,
            select: {
                type: 0,
                value: 15,
                unit: 'hours'
            },
            viewmode: 0
        });
        const { types, units, rangeShortcuts, getRange } = useDateRange();
        const { $dayjs, i18n } = useContext();
        $dayjs.extend(relativeTime);
        const onShortcutClick = (shortcut) => {
            const range = shortcut.onClick();
            const timeToNow = $dayjs(range[0]).toNow(true).split(' ');
            state.select.value = parseInt(timeToNow[0] === 'a' ? '1' : timeToNow[0]);
            state.select.unit =
                timeToNow[1].slice(-1) === 's' ? timeToNow[1] : `${timeToNow[1]}s`;
            emit('select', { range, text: shortcut.text });
            state.menu = false;
        };
        const onApplyClick = () => {
            const range = getRange(state.select.type, state.select.value, state.select.unit);
            emit('select', {
                range,
                text: `${state.select.type === 0 ? i18n.t('last') : i18n.t('next')} ${state.select.value} ${i18n.t(state.select.unit)}`
            });
            state.menu = false;
        };
        const onNextWindow = () => {
            emit('click:next-window');
            state.menu = false;
        };
        const onPreviousWindow = () => {
            emit('click:previous-window');
            state.menu = false;
        };
        const onChangeViewMode = (type) => {
            localStorage.setItem('range_picker_view_mode', type);
            emit('update:view-mode', type);
        };
        onMounted(() => {
            if (props.defaultRange) {
                onShortcutClick(rangeShortcuts[Common[props.defaultShortcut]]);
            }
            const cachedViewMode = localStorage.getItem('range_picker_view_mode');
            state.viewmode = 0;
            if (cachedViewMode === 'relative') {
                state.viewmode = 1;
                emit('update:view-mode', cachedViewMode);
            }
        });
        watch(() => props.defaultShortcut, () => onShortcutClick(rangeShortcuts[Common[props.defaultShortcut]]));
        return {
            ...toRefs(state),
            types,
            units,
            rangeShortcuts,
            onShortcutClick,
            onApplyClick,
            onNextWindow,
            onPreviousWindow,
            onChangeViewMode
        };
    }
});
